/**
 *
 * These styles are solely for adding [background images] or
 * [background colors] to blocks.
 *
 */

import heroBg from './assets/herobg.png'
import cc from './assets/cc.png'
import cccta from './assets/cc-cta.png'
import heroBg2 from './assets/contour-pattern.svg'
import check from './assets/check.png'
import marketer from './assets/marketer.png'
import roi from './assets/roi.jpg'
import sec3e from './assets/img-svc-section-3e.png'
import sec3d from './assets/img-svc-section-3d.png'

export default {
  html: {
    overflowX: 'hidden'
  },
  body : {
    overflowX: 'hidden'
  },
  footer: {
    display:"block",
    overflowX: "hidden"
  },
  heroContainer: {
    position: `relative`,
    '::before': {
      content: `" "`,
      width: `full`,
      height: `150%`,
      position: `absolute`,
      top: 0,
      left: 0,
      zIndex: -1,
      background: t =>
        `url(${sec3d}) no-repeat center 0, linear-gradient(
          180deg,
          ${t.colors.alphaLighter} 0%,
          ${t.colors.background} 100%
        )`,
      backgroundSize: `100%`
    }
  },
}
